import Vue from 'vue'
import Router from 'vue-router'
import First from '../pages/First.vue'
import Second from '../pages/Second'
import Three from '../pages/Three'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/first',
      component: First
    },
    {
      path: '/',
      redirect: '/first'
    },
    {
      path: '/second',
      component: Second
    },
    {
      path: '/three',
      component: Three
    }
  ]
})



