<template>
  <div class="dept-body">
    <div style="height: 100%" class="main-split">
      <!-- <el-tabs slot="left" class="dept-tree-tabs" type="border-card" style="height: 100%" v-model="leftActiveName">
            <el-tab-pane :label="deptname" name="tree" v-if="treedept=='treedept'||treedept==null" class="defaultTree" style="height: calc(100% + 24px);margin: -12px 0 0 -12px;">
              <qz-treedept :isLimitShow="false" style="height: 100%" ref="tvwjg" />
            </el-tab-pane>
            <slot></slot>
          </el-tabs> -->
      <div slot="right" id="right" style="height:100%;margin:12px">
        <el-form ref="dataForm" :model="dataForm" label-width="130px">
          <el-row>
            <el-col :span="10">
              <el-form-item label="人员姓名:">
                <el-input v-model="dataForm.name" size="small"></el-input>
              </el-form-item>
              <el-form-item label="身份证号:">
                <el-input v-model="dataForm.idNumber" size="small"></el-input>
              </el-form-item>
              <el-form-item label="人员类别:">
                <el-radio-group v-model="dataForm.category" size="small">
                  <el-radio-button v-for="item in cateoryList" :key="item.value" :label="item.value">{{item.name}}</el-radio-button>
                  <!-- <el-radio-button :label="0">全部</el-radio-button>
                      <el-radio-button :label="1">公务员</el-radio-button>
                      <el-radio-button :label="2">非公务员</el-radio-button> -->
                </el-radio-group>
              </el-form-item>
              <el-form-item label="女少非:">
                <el-checkbox-group v-model="dataForm.nsf" size="small">
                  <el-checkbox-button :label="0">女性</el-checkbox-button>
                  <el-checkbox-button :label="1">少数民族</el-checkbox-button>
                  <el-checkbox-button :label="2">非党员</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="民族:">
                <el-radio-group v-model="dataForm.national" size="small">
                  <el-radio-button :label="0">全部</el-radio-button>
                  <el-radio-button :label="1">汉族</el-radio-button>
                  <el-radio-button :label="2">非汉族</el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="学历:">
                <el-checkbox-group v-model="dataForm.education" size="small">
                  <el-checkbox-button :label="0">全部</el-checkbox-button>
                  <el-checkbox-button :label="1">研究生</el-checkbox-button>
                  <el-checkbox-button :label="2">大学本科</el-checkbox-button>
                  <el-checkbox-button :label="3">大学专科</el-checkbox-button>
                  <el-checkbox-button :label="4">高中及以下</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="学位:">
                <el-checkbox-group v-model="dataForm.degree" size="small">
                  <el-checkbox-button :label="0">全部</el-checkbox-button>
                  <el-checkbox-button :label="1">博士</el-checkbox-button>
                  <el-checkbox-button :label="2">硕士</el-checkbox-button>
                  <el-checkbox-button :label="3">学士</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="全日制学历:">
                <el-checkbox-group v-model="dataForm.fullTimeEducation" size="small">
                  <el-checkbox-button :label="0">全部</el-checkbox-button>
                  <el-checkbox-button :label="1">研究生</el-checkbox-button>
                  <el-checkbox-button :label="2">大学本科</el-checkbox-button>
                  <el-checkbox-button :label="3">大学专科</el-checkbox-button>
                  <el-checkbox-button :label="4">高中及以下</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="在职学历:">
                <el-checkbox-group v-model="dataForm.jobEducation" size="small">
                  <el-checkbox-button :label="0">全部</el-checkbox-button>
                  <el-checkbox-button :label="1">研究生</el-checkbox-button>
                  <el-checkbox-button :label="2">大学本科</el-checkbox-button>
                  <el-checkbox-button :label="3">大学专科</el-checkbox-button>
                  <el-checkbox-button :label="4">高中及以下</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="职务层级:">
                <el-checkbox-group v-model="dataForm.jobLevel" size="small">
                  <el-checkbox-button v-for="item in jobLevelList" :key="item.value" :label="item.value" @change="selectChange(item)">{{item.name}}</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="职务层次:">
                <el-checkbox-group v-model="dataForm.jobLevel1" size="small">
                  <el-checkbox-button v-for="item in jobLevelList1" :key="item.value" :label="item.value">{{item.name}}</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="年龄:">
                <el-input v-model="dataForm.startAge" size="small" style="width:45%"></el-input><tt style="margin: 0 10px">至</tt>
                <el-input v-model="dataForm.endAge" size="small" style="width:45%"></el-input>
              </el-form-item>
              <el-form-item label="任现职务层次年限:">
                <el-input v-model="dataForm.startYear" size="small" style="width:45%"></el-input><tt style="margin: 0 10px">至</tt>
                <el-input v-model="dataForm.endYear" size="small" style="width:45%"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item label="成员类别:">
                <el-checkbox-group v-model="dataForm.members" size="small">
                  <el-checkbox-button :label="0">正职</el-checkbox-button>
                  <el-checkbox-button :label="1">副职</el-checkbox-button>
                  <el-checkbox-button :label="2">其他</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="是否领导职务:">
                <el-radio-group v-model="dataForm.isleader" size="small">
                  <el-radio-button :label="0">全部</el-radio-button>
                  <el-radio-button :label="1">是</el-radio-button>
                  <el-radio-button :label="2">否</el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="任现职级年限:">
                <el-input v-model="dataForm.startRankYear" size="small" style="width:45%"></el-input><tt style="margin: 0 10px">至</tt>
                <el-input v-model="dataForm.endRankYear" size="small" style="width:45%"></el-input>
              </el-form-item>
              <el-form-item label="出生日期:">
                <el-date-picker v-model="dataForm.dateBirth" type="date" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="入党时间:">
                <el-date-picker v-model="dataForm.partyDate" type="date" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="参加工作时间:">
                <el-date-picker v-model="dataForm.workDate" type="date" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="年度考核:">
                <el-select v-model="dataForm.annualAppraisal" multiple filterable allow-create default-first-option placeholder="请选择">
                  <el-option v-for="item in annualAppraisalList" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="管理状态:">
                <el-radio-group v-model="dataForm.manageState" size="small">
                  <el-radio-button :label="0">全部</el-radio-button>
                  <el-radio-button :label="1">现职人员</el-radio-button>
                  <el-radio-button :label="2">非现职人员</el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="主要特点及不足:">
                <el-input v-model="dataForm.characteristics" size="small"></el-input>
              </el-form-item>
              <el-form-item label="熟悉领域:">
                <el-checkbox-group v-model="dataForm.familiarArea" size="small">
                  <el-checkbox-button v-for="item in familiarAreaList" :key="item.value" :label="item.value">{{item.name}}</el-checkbox-button>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="籍贯:">
                <el-input v-model="dataForm.nativePlace" size="small"></el-input>
              </el-form-item>
              <el-form-item label="简历:">
                <el-input v-model="dataForm.resume" size="small"></el-input>
              </el-form-item>
              <el-form-item label="毕业院校:">
                <el-input v-model="dataForm.graduateSchool" size="small"></el-input>
              </el-form-item>
              <el-form-item label="所学专业:">
                <el-input v-model="dataForm.professional" size="small"></el-input>
              </el-form-item>
              <el-form-item label="成员:">
                <el-input v-model="dataForm.characteristics" size="small"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="已选条件:">
                <el-tag v-for="tag in tags" closable :key="tag.name" :type="tag.type" @close="handleClose(tag)">
                  职务层级:{{tag.name}}
                </el-tag>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
// import resizemix from '@/components/qz-mixins/windowresize/resizemix'
export default {
  name: 'template',
  // mixins: [resizemix],
  // props: ['treedept'],
  data () {
    return {
      leftActiveName: 'tree',
      split1: 0.15,
      col: 6,
      deptname: '组织架构', // 潍坊市
      dataForm: {
        name: '',
        idNumber: '',
        category: '',
        nsf: [],
        national: '',
        education: [],
        degree: [],
        fullTimeEducation: [],
        jobEducation: [],
        jobLevel: [],
        jobLevel1: [],
        startAge: '',
        endAge: '',
        startYear: '',
        endYear: '',
        members: [],
        startRankYear: '',
        endRankYear: '',
        dateBirth: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        partyDate: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        workDate: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        annualAppraisal: [],
        manageState: '',
        characteristics: '',
        familiarArea: [],
        nativePlace: '',
        resume: '',
        graduateSchool: '',
        professional: ''
      },
      selectedConditions: [],
      cateoryList: [
        { value: '0', name: '全部' },
        { value: '1', name: '公务员' },
        { value: '2', name: '非公务员' }
      ],
      // 职务层级
      jobLevelList: [
        { value: '0', name: '全部' },
        { value: '1', name: '省部级正职' },
        { value: '2', name: '省部级副职' },
        { value: '3', name: '厅局级正职' },
        { value: '4', name: '厅局级副职' },
        { value: '5', name: '县处级正职' },
        { value: '6', name: '县处级副职' },
        { value: '7', name: '乡科级正职' },
        { value: '8', name: '乡科级副职' }
      ],
      // 职务层次
      jobLevelList1: [
        { value: '0', name: '全部' },
        { value: '1', name: '一级巡视员' },
        { value: '2', name: '二级巡视员' },
        { value: '3', name: '一级调研员' },
        { value: '4', name: '二级调研员' },
        { value: '5', name: '三级调研员' },
        { value: '6', name: '四级调研员' },
        { value: '7', name: '一级主任科员' },
        { value: '8', name: '二级主任科员' },
        { value: '9', name: '三级主任科员' },
        { value: '10', name: '四级主任科员' },
        { value: '11', name: '一级科员' },
        { value: '12', name: '二级科员' }
      ],
      // 年度考核
      annualAppraisalList: [{
        value: '0',
        label: '2019'
      }, {
        value: '1',
        label: '2018'
      }, {
        value: '2',
        label: '2017'
      }, {
        value: '3',
        label: '2016'
      }, {
        value: '4',
        label: '2015'
      }],
      // 熟悉领域
      familiarAreaList: [
        { value: '1', name: '党务工作' },
        { value: '2', name: '法制建设' },
        { value: '3', name: '纪检监察' },
        { value: '4', name: '人民团体' },
        { value: '5', name: '政务工作' },
        { value: '6', name: '发展改革' },
        { value: '7', name: '经贸信息' },
        { value: '8', name: '人事劳动' },
        { value: '9', name: '教育科技' },
        { value: '10', name: '公检法司' },
        { value: '11', name: '财税金融' },
        { value: '12', name: '闲土资源' },
        { value: '13', name: '城建交通' },
        { value: '14', name: '农林水渔' },
        { value: '15', name: '对外经贸' },
        { value: '16', name: '文化体育' },
        { value: '17', name: '卫生医药' },
        { value: '18', name: '环境保护' },
        { value: '19', name: '对外债务' },
        { value: '20', name: '民族宗教' },
        { value: '21', name: '行政监督' },
        { value: '22', name: '社会事务' },
        { value: '23', name: '高校党册' },
        { value: '24', name: '学生工作' },
        { value: '25', name: '后勤管理' },
        { value: '26', name: '科研管理' },
        { value: '27', name: '教学管理' },
        { value: '28', name: '企业管理' }
      ],
      tags: [
        // { name: '标签一', type: '' },
        // { name: '标签二', type: 'success' },
        // { name: '标签三', type: 'info' },
        // { name: '标签四', type: 'warning' },
        // { name: '标签五', type: 'danger' }
      ]
    }
  },
  mounted () {
  },
  watch: {
    selectedConditions () {
      console.log('321')
      let tempArr = Object.assign([], this.selectedConditions)
      // tempArr = this.delRepeat(tempArr)
      // console.log(tempArr)
      this.tags = tempArr
      console.log(this.tags)
    },
    'dataForm.jobLevel' () {
      this.moreArr()
    }
  },
  methods: {
    widthChange () {
      let infoCardWidth = document.getElementsByClassName('psn-info-card')[0].offsetWidth
      if (infoCardWidth < 750) {
        this.col = 24
      } else if (infoCardWidth < 1110) {
        this.col = 12
      } else if (infoCardWidth < 1500) {
        this.col = 8
      } else {
        this.col = 6
      }
    },
    init () {
    },
    clearAll () { // 全部清空数据
      this.tags = []
      this.dataForm.jobLevel = []
    },
    selectChange (item) {
      this.selectedConditions.push(item)
    },
    handleClose (tag) { // 标签的删除事件
      // 去掉当前删除的tag
      let jobLevel = this.dataForm.jobLevel

      this.dataForm.jobLevel = jobLevel.filter(item => {
        if (tag.value !== item) {
          return true
        }
      })
    },
    moreArr () {
      let jobLevel = this.dataForm.jobLevel
      let tempTags = []

      tempTags = this.baseDataDetail(jobLevel, this.jobLevelList, tempTags)
      this.selectedConditions = tempTags
    },
    baseDataDetail (jobLevel, baseData, callBack) { // 封装的数组方法
      let temp = callBack
      // 循环两个数据拿到选择的checkbox的id对应的初始数据
      jobLevel.forEach(item => {
        baseData.forEach(itemSecond => {
          if (item === itemSecond.value) {
            temp.push(itemSecond)
          }
        })
      })
      return temp
    }
    // selectChange (item) {
    //   this.tags.push(item)
    //   for (let i = 0; i < this.tags.length; i++) {
    //     // console.log(this.tags[i])
    //     for (let j = i + 1; j < this.tags.length; j++) {
    //       if (this.tags[i]['value'] == this.tags[j]['value']) { // 判断是否存在重复的
    //         // 将最后点击的id的name 赋值给现有的id里面的name
    //         this.tags[i]['name'] = this.tags[j]['name']
    //         this.tags.splice(j, 1) // 移除数据里面重复的
    //       }
    //     }
    //   }
    // },
    // handleClose (tag) { // 标签的删除事件
    //   // 去掉当前删除的tag
    //   let yourChoseTags = this.dataForm.jobLevel
    //   // let yourChoseTags1 = this.dataForm.jobLevel
    //   this.dataForm.jobLevel = yourChoseTags.filter(item => {
    //     if (tag.value !== item) {
    //       return true
    //     }
    //   })
    //   this.tags.splice(this.tags.indexOf(tag), 1)
    // }
  },
  computed: {}
}
</script>

<style scoped>
.dept-body {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}

.main-split {
  /*height: 600px;*/
  flex: 1;
  border: 1px solid #dcdee2;
}

>>> .el-tabs__content {
  height: 100%;
}

.dept-split-pane {
  padding: 10px;
}

.dept-tree-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main-split {
  border: 1px solid #dcdee2;
}

.dept-split-pane {
  padding: 10px;
}

.dept-split-grid {
  padding: 1px 2px 2px 7px;
}

.el-tabs__item {
  line-height: 30px;
  height: 30px;
}

>>> #right > .el-tabs__header {
  display: none;
}
>>> .el-checkbox-button,
.el-checkbox-button__inner {
  margin-right: 5px;
}
>>> .el-checkbox-button__inner {
  border: 1px solid #dcdfe6;
}
>>> .el-checkbox-button:first-child .el-checkbox-button__inner {
  border: 1px solid #dcdfe6;
}
>>> .el-radio-button,
.el-radio-button__inner {
  margin-right: 5px;
}
>>> .el-radio-button__inner {
  border: 1px solid #dcdfe6;
}
>>> .el-radio-button:first-child .el-radio-button__inner {
  border: 1px solid #dcdfe6;
}
>>> .el-form-item {
  margin-bottom: 10px;
}
</style>



